.main-header {
    .main-menu-item a {
        @apply transition-none;
    }

    .logo-light {
        display: none;
    }

    .header-text-theme {
        @apply border-dark-10;
    }

    &.light {

        .logo-light {
            display: flex;
        }

        .logo-dark {
            display: none;
        }

        .header-text-theme {
            @apply text-light border-light-20;
        }

        .menu-1 {
            .header-search-wrapper {
                input.input {
                    @apply md:border-light-50 md:hover:border-light md:focus:bg-light md:placeholder-light-75 md:focus:placeholder-dark;
                }

                input.input:focus ~ .search-input-icon {
                    @apply md:text-dark;
                }

                .search-input-icon {
                    @apply md:text-light-75;
                }
            }
        }

        .menu-2 {
            .header-search-wrapper {
                input.input {
                    @apply lg:border-light-50 lg:hover:border-light lg:focus:bg-light lg:placeholder-light-75 lg:focus:placeholder-dark;
                }

                input.input:focus ~ .search-input-icon {
                    @apply lg:text-dark;
                }

                .search-input-icon {
                    @apply lg:text-light-75;
                }
            }
        }

        .header-search-wrapper {
            .header-search-icon {
                @apply text-light;
            }
        }

        .main-menu-item {
            @apply hover:bg-light hover:text-brand;
        }

        .header-customer-icon,
        .js-icon-search,
        .menu-icon-wrapper {
            &:hover svg {
                @apply text-brand;
            }
        }

        .header-customer-icon,
        .header-icon-search-2,
        .menu-icon-wrapper svg {
            @apply text-light;
        }
    }

    &.dark {

        .logo-light {
            display: none;
        }

        .header-text-theme {
            @apply text-dark border-dark-10;
        }

        .menu-1 {
            .header-search-wrapper {
                input.input {
                    @apply md:placeholder-dark-60;
                }

                input.input:focus ~ .search-input-icon {
                    @apply md:text-dark;
                }

                .search-input-icon {
                    @apply md:text-dark-60;
                }
            }
        }

        .menu-2 {
            .header-search-wrapper {
                input.input {
                    @apply lg:placeholder-dark-60;
                }

                input.input:focus ~ .search-input-icon {
                    @apply lg:text-dark;
                }

                .search-input-icon {
                    @apply lg:text-dark-60;
                }
            }
        }

        .header-search-wrapper {
            input.input {
                @apply bg-transparent border-dark-30 focus:border-dark focus:bg-light focus:placeholder-dark;
            }

            .header-search-icon {
                @apply text-dark;
            }
        }

        .header-customer-icon,
        .header-icon-search-2,
        .menu-icon {
            @apply text-dark;
        }
    }
}

