@import "components/_aos";
@import "components/_basket";
@import "components/_button";
@import "components/_cookies";
@import "components/_container";
@import "components/_dropdown";
@import "components/_gallery";
@import "components/_glide";
@import "components/_header";
@import "components/_image_zoom";
@import "components/_modal";
@import "components/_nav-tabs";
@import "components/_product-card";
@import "components/_range_filter";
@import "components/_rich_text";
@import "components/_row";
@import "components/_shopify";
@import "components/_switcher";
@import "components/_tooltip";
/*INPUTS*/
@import "components/inputs/_color-input";
@import "components/inputs/_input";
@import "components/inputs/_inputs";
@import "components/inputs/_size-input";
@import "components/inputs/_toggle";

@layer components {
    .tab-pane {
        &:not(.active) {
            @apply hidden;
        }
    }

    .collapse-btn,
    .collapse-horizontal-btn {
        @apply flex justify-between items-center;

        svg {
            transition: transform .2s;
        }

        &:not(.collapsed) {
            svg {
                @apply rotate-180;
            }
        }
    }

    .collapse-horizontal {
        max-width: 50rem;
        opacity: 1;
        transition: max-width .2s ease-in-out, opacity .3s ease-in .1s;

        &.collapsed {
            max-width: 0;
            opacity: 0;
            overflow: hidden;
            transition: max-width .2s ease-in-out, opacity .2s ease-in;
        }
    }

    .bg-filter {
        @apply fixed left-0 top-0;
        @apply w-screen h-screen bg-dark-30;
        @apply opacity-0;
        opacity: 0;
        visibility: hidden;
        transition-property: opacity, visibility;
        transition-duration: .15s;
        transition-timing-function: ease-in-out;
    }

    .sidebar {
        @apply fixed top-0 left-0 w-full h-full;
        @apply bg-light;
        @apply -translate-x-full transition-transform duration-150 ease-in-out;
        z-index: 31;
        overflow: hidden;

        &.active {
            @apply translate-x-0;

            + .bg-filter {
                transition: opacity 0.15s ease-in-out, z-index 0s linear 0s;
                opacity: 1;
                visibility: visible;
                z-index: 30;
            }
        }

    }

    .sidebar-right {
        @apply right-0 translate-x-full;
        left: auto;
    }

    .burger-menu {
        max-width: 390px;
    }

    .js-mobile-menu-scroll {
        height: 100vh;
        overflow-y: auto;
        overflow-x: hidden;
    }

    .js-mobile-menu-sub-level {
        @apply absolute left-full top-0;
        @apply w-full h-full;
        @apply hidden;

        &.active {
            @apply block;
            height: 100vh;
        }
    }

    .color-detail {
        width: 100%;
        height: 100%;
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(50%, 1fr));
    }

    .google-map {
        width: 100%;
        height: 20.125rem;

        @screen max-md {
            height: 17.625rem;
        }

        iframe {
            width: 100%;
            height: 100%;
        }
    }

    .gallery-modal {

        .modal-dialog {
            max-width: 69.375rem;
            width: 90%;

            @screen max-sm {
                width: 100%;
                height: 100%;
                margin: 0;
                border-radius: 0;
            }

            &__quick-view {
                max-width: unset;
            }
        }

        .glide__arrow {
            @apply w-48 h-48;
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
        }

        .glide-image {
            border: 2px solid transparent;
            transition-property: border;
            transition-duration: .1s;
            transition-timing-function: ease-in;

            &.active {
                @apply border-brand;
            }
        }

        &--2 {
            .modal-dialog {
                width: 100%;
                max-width: unset;

                .modal-content {
                    border-radius: 0;

                    .glide-image {
                        border-width: 1px;
                    }
                }
            }
        }
    }

    .gallery-mobile-slider {

        @screen max-xs {
            width: 100vw;
            margin-left: -1.25rem;
        }

        .glide__bullet {
            transition: background-color .1s;

            &--active {
                @apply bg-dark-material;
            }
        }
    }

    .slides-recommended {
        padding-bottom: 130px;
    }

    .triangle {
        width: 0;
        height: 0;
        border: 8px solid transparent;
        border-top: 0;
        @apply border-b-light-surface;
    }

    .search-modal {
        max-height: calc(100vh - 8.75rem);
    }

    .catalog-description {

        &.collapse:not(.show) {
            display: block;
            overflow: hidden;

            > div {
                text-overflow: ellipsis;
                -webkit-line-clamp: 2;
                -webkit-box-orient: vertical;
                display: -webkit-box;
            }
        }

        &.collapse:not(.show),
        &.collapsing {
            /* 2 rows with 1rem font size and 1.5 line height */
            height: 2*1rem*1.5;
        }
    }

    .blog-main-item {
        @screen max-sm {
            width: 19.375rem; /*310px*/
        }
    }

    .contact-map {
        height: 15.625rem; /*250px*/

        @screen lg {
            min-height: 21.875rem; /*350px*/
            height: 80vh;
        }
    }

    .locator {

        @screen lg {
            min-height: 31.25rem;
            height: 80vh;
            max-height: 50rem;
        }
    }

    .store-detail {
        transform: translateX(0);
        opacity: 1;
        visibility: visible;
        transition: all .2s ease-in-out;

        &.collapsed {
            transform: translateX(-150%);
            opacity: 0;
            visibility: hidden;
        }
    }

    .custom-scrollbar {

        &::-webkit-scrollbar {
            width: 10px;
        }

        &::-webkit-scrollbar-track {
            @apply bg-light-surface;
            border: 1px solid;
            @apply border-light-material;
        }

        &::-webkit-scrollbar-thumb {
            @apply bg-dark-30 rounded-full;
            border: 3px solid transparent;
            background-clip: padding-box;
        }
    }

    .scrollbar-hide::-webkit-scrollbar {
        display: none;
    }

    /* For IE, Edge and Firefox */
    .scrollbar-hide {
        -ms-overflow-style: none; /* IE and Edge */
        scrollbar-width: none; /* Firefox */
    }

    .store-map {
        @screen max-md {
            height: 33.125rem;
        }
    }

    .store-detail-slider {
        height: 15rem; /*240px*/

        @screen md {
            height: 28.125rem; /*450px*/
        }

        .glide__bullet {
            transition: width .15s;

            &--active {
                @apply w-64;
                @apply bg-dark;
            }
        }
    }

    .tab-link-location {
        &.active {
            display: none;
        }
    }

    .newsletter-modal-image {
        @screen max-sm {
            height: 330px;
        }
    }

    .thumbnail {

        &:hover {

            &::after {
                opacity: 1;
            }
        }

        &::after {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            @apply bg-light-50;
            opacity: 0;
            @apply transition-opacity;
        }
    }

    /* Login and recover password forms */
    #recover,
    #recover + div {
        display: none;
    }

    #recover:target,
    #recover:target + div {
        display: block;
    }

    #recover:target ~ #login,
    #recover:target ~ #login + div {
        display: none;
    }

    #recover,
    #login {
        scroll-margin-top: 20rem;
    }

    /*Cart item*/
    .cart__empty-text,
    .is-empty .cart__contents,
    cart-items.is-empty .title-wrapper-with-link,
    .is-empty.cart-hide-when-empty,
    .is-empty .cart-hide-when-empty {
        display: none;
    }

    .is-empty .cart__empty-text,
    .is-empty .cart__warnings {
        display: block;
    }

    .cart-item__error-text:empty + svg {
        display: none;
    }

    .is-empty .cart-when-empty {
        @apply pb-64;
    }

    /* Product gallery */
    .grid--peek .grid__item {
        min-width: 35%;
    }

    cart-drawer {
        &.active {
            transform: translateX(0);
        }
    }

    .js-show-more-button {
        svg {
            transition: rotate .2s;
        }

        &.active {
            svg {
                transform: rotate(180deg);
            }
        }
    }

    .cl-link {

        &.active {

            .cl-link-title {
                opacity: 1;
            }

            .btn {
                opacity: 1;
                visibility: visible;
            }
        }
    }

    .cl-image {

        &.active {
            opacity: 1;
            visibility: visible;
            z-index: 2;
        }
    }

    img.lazyload:not([src]) {
        visibility: hidden;
    }

    .menu-1 + .header-banner {
        @apply -mt-61 md:-mt-154;
    }

    .menu-2 + .header-banner {
        @apply -mt-61 md:-mt-97;
    }

    .product-description {
        a {
            @apply text-brand underline;
        }
    }
}