  border-color: var(--color-dark-10);
}
.main-header.light .logo-light {
            display: flex;
        }
.main-header.light .logo-dark {
            display: none;
        }
.main-header.light .header-text-theme {
  border-color: var(--color-light-20);
  color: var(--color-light);
}
@media (min-width: 768px) {

  .main-header.light .menu-1 .header-search-wrapper input.input {
    border-color: var(--color-light-50);
  }

  .main-header.light .menu-1 .header-search-wrapper input.input::-moz-placeholder {
    color: var(--color-light-75);
  }

  .main-header.light .menu-1 .header-search-wrapper input.input::placeholder {
    color: var(--color-light-75);
  }

  .main-header.light .menu-1 .header-search-wrapper input.input:hover {
    border-color: var(--color-light);
  }

  .main-header.light .menu-1 .header-search-wrapper input.input:focus {
    background-color: var(--color-light);
  }

  .main-header.light .menu-1 .header-search-wrapper input.input:focus::-moz-placeholder {
    color: var(--color-dark);
  }

  .main-header.light .menu-1 .header-search-wrapper input.input:focus::placeholder {
    color: var(--color-dark);
  }

  .main-header.light .menu-1 .header-search-wrapper input.input:focus ~ .search-input-icon {
    color: var(--color-dark);
  }

  .main-header.light .menu-1 .header-search-wrapper .search-input-icon {
    color: var(--color-light-75);
  }
}
@media (min-width: 992px) {

